.App {
  font-family: sans-serif;
  text-align: center;
}


.container {
  width: 100%;
}

.panel {
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
}

.lastContainer {
  padding: 50px;
}



.services-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 50px;
  padding: 50px 20px;
}

.service-item {
  position: relative;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transform-origin: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 250px;
  overflow: hidden;
  
}

.service-item:nth-child(1) {
  background-image: url("/src/images/STARLIGHT.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.service-item:nth-child(2) {
  background-image: url("/src/images/lunar.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
}

.service-item:nth-child(3) {
  background-image: url("/src/images/elements.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.service-item:nth-child(4) {
  background-image: url("/src/images/wings.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.service-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  z-index: 1;
  filter: brightness(50%);
}

.service-item:hover .service-description {
  opacity: 1;
}

.service-description {
  font-size: 1rem;
  color: #fff;
  max-width: 80%;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 2;
  bottom: 20px;
  left: 50%;
}

.service-title {
  font-size: 1.8rem;
  color: #fff;
  font-weight: bold;
  margin-bottom: 15px;
  z-index: 3;
}

@font-face {
