.lizzie {
  font-family: "khuja";
  letter-spacing: 1rem;
  font-weight: bold;
  background: linear-gradient(to bottom, #7e4bb2, #392d69); /* Gradient colors */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent; /* This makes the gradient text visible */
}

